#logindiv {
  max-width: 600px;
  margin: 5vh auto;
  padding: 10px;
  text-align: center;
  color: black;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}

.saplogodivlogin {
  background: url(../images/saplogonew.png);
  background-repeat: no-repeat;
  background-size: cover;
  width: 140px;
  height: 70px;
  margin: auto;
  z-index: 100;
  
}

#logindiv h2 {
  margin: 3vh;
  text-align: center;
  font-size: 2rem;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-weight: 400;
}

.loginform {
	display: grid; 
	grid-template-columns: auto;
	gap: 1vh;
}

.logintext {
	margin: 1.5vh auto;
	width: 20vw;
	height: 4vh;
	font-size: 1.2rem;
	border-radius: 4px;
	border: 1px 1px 0.8px 0.8px solid black;
	padding: 10px;
	color: black !important;
	z-index: 100;
}

.logindiv h3 {
	font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
	font-size: 1.2rem;
}

.loginbutton {
  height: 5vh;
  width: 10vw;
  font-size: 1.2rem;
  margin: 2vh auto;
  background-color: #1294F2;
  border-radius: 5px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-style: normal;
  font-weight: 100;
  line-height: normal;
}

.loginbutton:hover{
	background-color: white;
	color: #1294F2;
	cursor: pointer;
}

.loginenforcer {
	margin: 40vh auto;
	width: 30vw;
	font-size: 1.7rem;
	text-align: center;
}

.loginenforcer a {
	text-decoration: none;
	color: #1294F2;
}

.registerlink {
	text-decoration: none;
	color: #1294F2;
}

